import { Ticket, TicketFormat, TicketImage, TicketType } from '@repay/api-sdk';
import classnames from 'classnames';
import React from 'react';
import { FormattedMessage } from 'react-intl';
import * as yup from 'yup';

import { translations } from '@/locales';
import { isTicketTypeSeason } from '@/utils/is-ticket-type-season';

import { useModal } from '@/hooks/useModal';
import { ProgressManager } from '@/hooks/useProgressManager';
import { apiClient } from '@/services/api-client';

import { BackButton } from '@/components/BackButton';
import { Form } from '@/components/Form';
import { ValidatedField } from '@/components/Form/ValidatedField';
import { HelpButton } from '@/components/HelpButton';
import { Modal } from '@/components/Modal';
import { Step, StepFooter, StepHeader } from '@/components/Step';
import { SubmitButton } from '@/components/SubmitButton';

import { TicketImageHelpModal } from '../modals/TicketImageHelpModal';
import { TicketImageUpload } from './TicketImageUpload';

const getImageTypesForTicket = (ticket: Pick<Ticket, 'format' | 'type'>) => {
  if (ticket.format === TicketFormat.BARCODE) return [TicketImage.TICKET];
  if (ticket.format === TicketFormat.SMARTCARD) return [];
  if (isTicketTypeSeason(ticket.type)) return [TicketImage.TICKET, TicketImage.DEFACED, TicketImage.PHOTOCARD];
  return [TicketImage.TICKET, TicketImage.DEFACED];
};

export const TicketImageStep: React.FC = () => {
  const { state, back, updateTicketImages } = ProgressManager.useContainer();

  const { initialValues, schema } = React.useMemo(() => {
    const initialValues = {
      tickets: state.tickets.map(({ number, utn, scn, type, format, image }) => ({
        id: number ?? utn ?? scn!,
        type,
        format,
        [TicketImage.TICKET]: image?.[TicketImage.TICKET],
        [TicketImage.DEFACED]: image?.[TicketImage.DEFACED],
        [TicketImage.PHOTOCARD]: image?.[TicketImage.PHOTOCARD]
      }))
    };

    const schema = yup
      .object({
        tickets: yup
          .array()
          .of(
            yup
              .object({
                id: yup.string().required(),
                type: yup.string().optional(),
                format: yup.string().required(),
                [TicketImage.TICKET]: yup
                  .mixed<File | string>()
                  .test({
                    test: (value) => !value || typeof value === 'string' || value instanceof File,
                    message: translations.customValidation.fileType
                  })
                  .test({
                    test: (value) =>
                      !value || typeof value === 'string' || (value instanceof File && value.size <= 10 * 1000 * 1000),
                    message: translations.customValidation.fileSize,
                    params: { size: 10 }
                  })
                  .when(['type', 'format'], {
                    is: (type: TicketType, format: TicketFormat) =>
                      getImageTypesForTicket({ type, format }).includes(TicketImage.TICKET),
                    then: (s) => s.required()
                  })
                  .label(translations.fields.ticketImage),
                [TicketImage.DEFACED]: yup
                  .mixed<File | string>()
                  .test({
                    test: (value) => !value || typeof value === 'string' || value instanceof File,
                    message: translations.customValidation.fileType
                  })
                  .test({
                    test: (value) =>
                      !value || typeof value === 'string' || (value instanceof File && value.size <= 10 * 1000 * 1000),
                    message: translations.customValidation.fileSize,
                    params: { size: 10 }
                  })
                  .when(['type', 'format'], {
                    is: (type: TicketType, format: TicketFormat) =>
                      getImageTypesForTicket({ type, format }).includes(TicketImage.DEFACED),
                    then: (s) => s.required()
                  })
                  .label(translations.fields.ticketDefacedImage),
                [TicketImage.PHOTOCARD]: yup
                  .mixed<File | string>()
                  .test({
                    test: (value) => !value || typeof value === 'string' || value instanceof File,
                    message: translations.customValidation.fileType
                  })
                  .test({
                    test: (value) =>
                      !value || typeof value === 'string' || (value instanceof File && value.size <= 10 * 1000 * 1000),
                    message: translations.customValidation.fileSize,
                    params: { size: 10 }
                  })
                  .when(['type', 'format'], {
                    is: (type: TicketType, format: TicketFormat) =>
                      getImageTypesForTicket({ type, format }).includes(TicketImage.PHOTOCARD),
                    then: (s) => s.required()
                  })
                  .label(translations.fields.ticketPhotocardImage)
              })
              .required()
          )
          .length(state.tickets.length)
          .required()
          .label(translations.fields.ticketImage)
      })
      .required();

    return { initialValues, schema };
  }, [state.tickets]);

  const onSubmit = React.useCallback(
    async ({ tickets }: yup.InferType<typeof schema>) => {
      const responses = await Promise.all(
        tickets.map(async ({ id, type, format, ...image }) => {
          return Object.entries(image).reduce<Promise<Partial<Record<TicketImage, string>>>>(
            async (promise, [key, file]) => {
              const type = key as TicketImage;
              const data = await promise;

              if (!file) return data;
              if (typeof file === 'string') return { ...data, [type]: file };

              const { url, headers } = await apiClient.ticket.getTicketImageUrl({
                id,
                type,
                filename: file.name
              });

              await fetch(url, { method: 'put', headers, body: file });

              return { ...data, [type]: url };
            },
            Promise.resolve({})
          );
        })
      );

      return updateTicketImages(responses.map((image) => image));
    },
    [updateTicketImages]
  );

  const modal = useModal(TicketImageHelpModal);

  return (
    <React.Fragment>
      <Step>
        <StepHeader title={<FormattedMessage id={translations.pages.progress.ticketImages.title} />} />

        <Form {...{ initialValues, schema, onSubmit }}>
          {({ submitting }) => (
            <React.Fragment>
              {state.tickets.some((t) => t.type === TicketType.RETURN) && (
                <div className="mb-8">
                  To enable us to process your refund as quickly as possible please upload an image of your Return
                  ticket with both portions (Outward and Return).
                </div>
              )}

              <div className="grid max-w-4xl gap-4 md:grid-cols-2">
                {state.tickets.map((ticket, index) => {
                  const imageTypes = getImageTypesForTicket(ticket);

                  return (
                    <div key={index} className={classnames('space-y-2', imageTypes.length > 1 && 'md:col-span-2')}>
                      <div className="text-sm">
                        <FormattedMessage
                          id={translations.pages.progress.ticketImages.ticketNumber}
                          values={{ number: ticket.number ?? ticket.utn ?? ticket.scn }}
                        />
                      </div>

                      <div className={classnames('grid gap-4', imageTypes.length > 1 && 'md:grid-cols-2')}>
                        {imageTypes.map((imageType) => (
                          <div key={imageType} className="relative">
                            <ValidatedField
                              field={TicketImageUpload}
                              id={`ticket-image-${index + 1}-${imageType}`}
                              name={`tickets[${index}].${imageType}`}
                              readOnly={submitting}
                            >
                              <FormattedMessage
                                id={translations.pages.progress.ticketImages.add[imageType]}
                                values={{ count: 1 }}
                              />
                            </ValidatedField>

                            <HelpButton
                              className="absolute top-4 right-4 -my-[2px]"
                              onClick={() =>
                                modal.open(() => null, {
                                  imageType,
                                  count: 1,
                                  refundReason: state.reason?.reason
                                })
                              }
                            />
                          </div>
                        ))}
                      </div>
                    </div>
                  );
                })}
              </div>

              <StepFooter>
                <SubmitButton loading={submitting}>
                  <FormattedMessage id={translations.buttons.continue} />
                </SubmitButton>

                <BackButton onClick={() => back()} />
              </StepFooter>
            </React.Fragment>
          )}
        </Form>
      </Step>

      <Modal {...modal.props} />
    </React.Fragment>
  );
};
