import { PurchaseMethod } from '@repay/api-sdk';
import React from 'react';
import { FormattedMessage } from 'react-intl';
import * as yup from 'yup';

import { ProgressManager } from '@/hooks/useProgressManager';
import { translations } from '@/locales';
import { apiClient } from '@/services/api-client';

import { Alert } from '@/components/Alert';
import { BackButton } from '@/components/BackButton';
import { Form } from '@/components/Form';
import { InputWithLabel } from '@/components/Form/InputWithLabel';
import { ValidatedField } from '@/components/Form/ValidatedField';
import { Step, StepFooter, StepHeader } from '@/components/Step';
import { SubmitButton } from '@/components/SubmitButton';

const schema = yup
  .object({
    bankName: yup.string().label(translations.fields.bankName).required(),
    fullName: yup.string().label(translations.fields.fullName).required(),
    accountNumber: yup.string().label(translations.fields.accountNumber).required(),
    sortCode: yup.string().label(translations.fields.sortCode).required()
  })
  .required();

export const BankDetailsStep: React.FC = () => {
  const { state, back, updateBankDetails } = ProgressManager.useContainer();

  const onSubmit = React.useCallback(
    async (values: yup.InferType<typeof schema>) => {
      await apiClient.paymentMethod.validateBankAccount({ requestBody: values });
      return updateBankDetails(values);
    },
    [updateBankDetails]
  );

  return (
    <React.Fragment>
      <Step>
        <StepHeader title={<FormattedMessage id={translations.pages.progress.purchaseMethod.bankTransfer.title} />} />

        <Form
          {...{
            initialValues: {
              fullName: [state.customer?.firstName, state.customer?.lastName].filter(Boolean).join(' '),
              ...state.payment?.[PurchaseMethod.BANK_TRANSFER]
            },
            schema,
            onSubmit
          }}
        >
          {({ submitting, submitError }) => (
            <div>
              <div className="space-y-4">
                <Alert type="info" title="Test instructions:">
                  <div>
                    Account number: <span className="font-medium">26207729</span>
                  </div>
                  <div>
                    Sort code: <span className="font-medium">560029</span>
                  </div>
                </Alert>

                {!!submitError && (
                  <Alert type="error" title="Bank account details are invalid!">
                    Please double check the data you introduced are correct.
                  </Alert>
                )}

                <div className="grid gap-4 md:grid-cols-2">
                  <ValidatedField
                    field={InputWithLabel}
                    id="bank-name"
                    name="bankName"
                    type="text"
                    label={<FormattedMessage id={translations.fields.bankName} />}
                    disabled={submitting}
                  />

                  <ValidatedField
                    field={InputWithLabel}
                    id="full-name"
                    name="fullName"
                    type="text"
                    label={<FormattedMessage id={translations.fields.fullName} />}
                    disabled={submitting}
                  />

                  <ValidatedField
                    field={InputWithLabel}
                    id="account-number"
                    name="accountNumber"
                    type="text"
                    label={<FormattedMessage id={translations.fields.accountNumber} />}
                    disabled={submitting}
                  />

                  <ValidatedField
                    field={InputWithLabel}
                    id="sort-code"
                    name="sortCode"
                    type="text"
                    label={<FormattedMessage id={translations.fields.sortCode} />}
                    disabled={submitting}
                  />
                </div>
              </div>

              <StepFooter>
                <SubmitButton loading={submitting}>
                  <FormattedMessage id={translations.buttons.continue} />
                </SubmitButton>

                <BackButton onClick={() => back()} />
              </StepFooter>
            </div>
          )}
        </Form>
      </Step>
    </React.Fragment>
  );
};
