import { BanknotesIcon, BuildingLibraryIcon, CreditCardIcon, DocumentTextIcon } from '@heroicons/react/24/outline';
import { PurchaseMethod } from '@repay/api-sdk';
import React from 'react';
import { Field } from 'react-final-form';
import { FormattedMessage } from 'react-intl';
import * as yup from 'yup';

import { ProgressManager } from '@/hooks/useProgressManager';
import { translations } from '@/locales';

import { BackButton } from '@/components/BackButton';
import { Form } from '@/components/Form';
import { RadioSelect } from '@/components/Form/RadioSelect';
import { Step, StepFooter, StepHeader } from '@/components/Step';
import { SubmitButton } from '@/components/SubmitButton';

const methods = [
  { Icon: CreditCardIcon, value: PurchaseMethod.PLASTIC },
  { Icon: BanknotesIcon, value: PurchaseMethod.CASH },
  { Icon: BuildingLibraryIcon, value: PurchaseMethod.BANK_TRANSFER },
  { Icon: DocumentTextIcon, value: PurchaseMethod.WARRANT }
];

const schema = yup
  .object({
    method: yup.mixed<PurchaseMethod>().oneOf(Object.values(PurchaseMethod)).required()
  })
  .required();

export const PurchaseMethodStep: React.FC = () => {
  const { state, back, updatePurchaseMethod } = ProgressManager.useContainer();

  const onSubmit = React.useCallback(
    (values: yup.InferType<typeof schema>) => updatePurchaseMethod(values.method),
    [updatePurchaseMethod]
  );

  return (
    <React.Fragment>
      <Step>
        <StepHeader title={<FormattedMessage id={translations.pages.progress.purchaseMethod.title} />} />

        <Form {...{ initialValues: { method: state.payment?.method }, schema, onSubmit }}>
          {({ submitting }) => (
            <div>
              <div className="grid max-w-4xl gap-4 md:grid-cols-2">
                <div>
                  <div className="space-y-4">
                    {methods.map(({ Icon, value }) => (
                      <Field key={value} id={`refund-reason-${value}`} name="method" type="radio" {...{ value }}>
                        {({ input, meta, ...props }) => (
                          <RadioSelect {...props} {...input} className="relative" contentClassName="flex items-center">
                            <Icon className="mr-4 h-5 w-5" />
                            <FormattedMessage id={translations.enum.purchaseMethod[value]} />
                          </RadioSelect>
                        )}
                      </Field>
                    ))}
                  </div>
                </div>
              </div>

              <StepFooter>
                <SubmitButton loading={submitting}>
                  <FormattedMessage id={translations.buttons.continue} />
                </SubmitButton>

                <BackButton onClick={() => back()} />
              </StepFooter>
            </div>
          )}
        </Form>
      </Step>
    </React.Fragment>
  );
};
