import { PurchaseMethod } from '@repay/api-sdk';
import React from 'react';
import { FormattedMessage } from 'react-intl';
import * as yup from 'yup';

import { ProgressManager } from '@/hooks/useProgressManager';
import { translations } from '@/locales';

import { BackButton } from '@/components/BackButton';
import { Form } from '@/components/Form';
import { DateInput } from '@/components/Form/DateInput';
import { InputWithLabel } from '@/components/Form/InputWithLabel';
import { MaskedInput } from '@/components/Form/MaskedInput';
import { ValidatedField } from '@/components/Form/ValidatedField';
import { Step, StepFooter, StepHeader } from '@/components/Step';
import { SubmitButton } from '@/components/SubmitButton';

const schema = yup
  .object({
    cardNumber: yup.string().label(translations.fields.cardNumber).required(),
    cardholderName: yup.string().label(translations.fields.cardholderName).required(),
    expiryDate: yup.string().label(translations.fields.cardExpiryDate).required(),
    cvv: yup.string().label(translations.fields.cardCVV).required()
  })
  .required();

export const CardDetailsStep: React.FC = () => {
  const { state, back, updateCardDetails } = ProgressManager.useContainer();

  const onSubmit = React.useCallback(
    (values: yup.InferType<typeof schema>) => updateCardDetails(values),
    [updateCardDetails]
  );

  return (
    <React.Fragment>
      <Step>
        <StepHeader title={<FormattedMessage id={translations.pages.progress.purchaseMethod.card.title} />} />

        <Form
          {...{
            initialValues: {
              cardholderName: [state.customer?.firstName, state.customer?.lastName].filter(Boolean).join(' '),
              ...state.payment?.[PurchaseMethod.PLASTIC]
            },
            schema,
            onSubmit
          }}
        >
          {({ submitting }) => (
            <div>
              <div className="grid gap-4 md:grid-cols-2">
                <ValidatedField
                  field={InputWithLabel}
                  as={MaskedInput}
                  mask="0000 0000 0000 0000"
                  id="card-number"
                  name="cardNumber"
                  type="text"
                  label={<FormattedMessage id={translations.fields.cardNumber} />}
                  disabled={submitting}
                />

                <ValidatedField
                  field={InputWithLabel}
                  id="cardholder-name"
                  name="cardholderName"
                  type="text"
                  label={<FormattedMessage id={translations.fields.cardholderName} />}
                  disabled={submitting}
                />

                <ValidatedField
                  field={InputWithLabel}
                  as={DateInput}
                  mask="MM/YYYY"
                  id="expiry-date"
                  name="expiryDate"
                  type="text"
                  label={<FormattedMessage id={translations.fields.cardExpiryDate} />}
                  disabled={submitting}
                />

                <ValidatedField
                  field={InputWithLabel}
                  as={MaskedInput}
                  mask="000"
                  id="cvv"
                  name="cvv"
                  type="text"
                  label={<FormattedMessage id={translations.fields.cardCVV} />}
                  disabled={submitting}
                />
              </div>

              <StepFooter>
                <SubmitButton loading={submitting}>
                  <FormattedMessage id={translations.buttons.continue} />
                </SubmitButton>

                <BackButton onClick={() => back()} />
              </StepFooter>
            </div>
          )}
        </Form>
      </Step>
    </React.Fragment>
  );
};
