import { Customer } from '@repay/api-sdk';
import React from 'react';
import { FormattedMessage } from 'react-intl';
import * as yup from 'yup';

import { translations } from '@/locales';

import { BackButton } from '@/components/BackButton';
import { Form } from '@/components/Form';
import { InputWithLabel } from '@/components/Form/InputWithLabel';
import { PhoneInput } from '@/components/Form/PhoneInput';
import { ValidatedField } from '@/components/Form/ValidatedField';
import { Step, StepFooter, StepHeader } from '@/components/Step';
import { SubmitButton } from '@/components/SubmitButton';

const schema = yup
  .object()
  .shape(
    {
      phone: yup
        .string()
        .phone()
        .when('email', { is: (value?: string) => value && value.length > 1, otherwise: (s) => s.required() })
        .label(translations.fields.phoneNumber),
      email: yup
        .string()
        .email()
        .when('phone', { is: (value?: string) => value && value.length > 1, otherwise: (s) => s.required() })
        .label(translations.fields.emailAddress)
    },
    [['phone', 'email']]
  )
  .required() as yup.SchemaOf<{ phone?: string; email?: string }>;

interface Props {
  customer?: Partial<Pick<Customer, 'email' | 'phone'>>;
  onBack(): void;
  onSubmit(userIdentifier: { email?: string; phone?: string }): Promise<void>;
}

export const LoginStep: React.FC<Props> = ({ customer, onBack, onSubmit }) => (
  <Step>
    <StepHeader title={<FormattedMessage id={translations.pages.progress.customerDetails.login.title} />} />

    <Form {...{ initialValues: { ...customer }, schema, onSubmit }}>
      {({ submitting }) => (
        <React.Fragment>
          <div className="grid gap-4 md:grid-cols-2">
            <ValidatedField
              field={InputWithLabel}
              as={PhoneInput}
              id="phone"
              name="phone"
              type="text"
              label={<FormattedMessage id={translations.fields.phoneNumber} />}
              disabled={submitting}
              focused
            />

            <ValidatedField
              field={InputWithLabel}
              id="email"
              name="email"
              type="text"
              label={<FormattedMessage id={translations.fields.emailAddress} />}
              disabled={submitting}
            />
          </div>

          <StepFooter>
            <SubmitButton loading={submitting}>
              <FormattedMessage id={translations.buttons.continue} />
            </SubmitButton>

            <BackButton onClick={onBack} />
          </StepFooter>
        </React.Fragment>
      )}
    </Form>
  </Step>
);
